import React, { Component } from 'react'
import './style.css'

export default class ShopItem extends Component {
    render() {
        const  { url, pic, shop, star, price, 
            quantitty, region, catagory } = this.props.data
        return (
            <a href={url} className='shopItem'>
                <div className='shopItem__pic' 
                style={{'backgroundImage':'url('+pic+')'}}></div>
                <div className='shopItem__content'>
                   <div className='shopItem__title'>{shop}</div>
                   <div className='shopItem__comment'>
                        <span className={'shopItem__star shopItem__star--'+star}></span>
                        <span className='shopItem__quantity'>{quantitty}</span>
                        <span className='shopItem__price'>{price}</span>
                    </div> 
                   <div className='shopItem__info'>
                        <span className='shopItem__region'>{region}</span>
                        <span className='shopItem__catagory'>{catagory}</span>
                    </div> 
                </div>
            </a>
        )
    }
}
